@import '../../styles/variables.scss';

.login-container {
    display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Asegura que ocupe al menos toda la altura de la pantalla */
  padding: 20px; /* Espacio interno */
  background-color: $primary-bg
  }

  .form-container {
    width: 100%;
    max-width: 700px; /* Lmite de ancho */
    padding: 2rem;
    background-color: transparent;
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  }

  .login-header {
    color: white;
    font-size: 30px;
    font-weight: 700;
    align-items: center;
    display: flex;
    margin-bottom: 10px;
  }

  .submit-button {
    background-color: $primary-btn;
    border-radius: 8px;
    transition: 0.8s;
    position: relative;
    &:hover {
        transition: 0.4s;
        transform: scale(1.1);
        color: $primary-bg;
        background-color: rgb(60, 63, 70);
        box-shadow: 0 8px 32px 0 rgb(170, 176, 189);
    }
    
  }